
.settingVisitTime {
  height: 100vh;
  width: 100%;
  background-image: url(../../assets/img/bgPerfectInfo.png);
  background-size: 100% 100%;
  position: fixed;
}
.settingVisitTime .setting-visit-time-head-box {
    color: #ffffff;
    font-size: 16px;
    padding: 30px 20px;
}
.settingVisitTime .setting-visit-time-select-box {
    padding: 0px 20px;
}
.settingVisitTime .setting-visit-time-select-box .setting-visit-time-selectbg-box {
      height: 40px;
      border: 1px solid #fff;
      border-radius: 5px;
}
.settingVisitTime .setting-visit-time-select-box .setting-visit-time-selectbg-box .icon-rili1 {
        color: #fff;
        line-height: 40px;
        padding-left: 10px;
}
.settingVisitTime .setting-visit-time-select-box .setting-visit-time-selectbg-box .setting-visit-time-select-txt {
        color: #fff;
}
.settingVisitTime .setting-visit-time-select-box .setting-visit-time-selectbg-box .icon-zhankai2 {
        color: #fff;
        text-align: right;
}
.settingVisitTime .setting-visit-time-bottom-box {
    line-height: 20px;
    width: 100%;
    position: fixed;
    bottom: 14px;
}
.settingVisitTime .setting-visit-time-bottom-box .setting-visit-time-bottom-txt {
      color: #fff;
      padding-left: 20px;
      padding-right: 20px;
      font-size: 12px;
}
.settingVisitTime .setting-visit-time-bottom-box .toInfo {
      display: block;
      width: 90%;
      height: 45px;
      background: transparent;
      color: #ffffff;
      border: 1px solid #fff;
      border-radius: 22px;
      margin-top: 15px;
      outline: none;
      font-size: 18px;
}



