
@charset "UTF-8";
.perfectInfoContainer {
  position: fixed;
  /* 固定 */
  height: 100vh;
  width: 100%;
  background-image: url("../../assets/img/bgPerfectInfo.png");
  background-size: 100% 100%;
}
.perfectInfoContainer .perfectInfoTopBox {
    margin: 50px auto;
    width: 70%;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopName .perfectInfoTopNameSpan {
      color: #ffffff;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopName .perfectInfoTopNameInput {
      background-color: transparent;
      color: #fff;
      FILTER: alpha(opacity=0);
      /*androd*/
      appearance: none;
      /*下拉框去掉右侧图标*/
      -moz-appearance: none;
      -webkit-appearance: none;
      float: right;
      outline: none;
      text-align: center;
      width: 80%;
      border-bottom: 1px solid #fff;
      padding-bottom: 10px;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopDepartment {
      padding-top: 70px;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopDepartment .perfectInfoTopDepartmentSpan {
        color: #ffffff;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopDepartment .perfectInfoTopDepartmentSelect {
        float: right;
        color: #ffffff;
        width: 80%;
        border: none;
        border-bottom: 1px solid #ffffff;
        padding-bottom: 10px;
        padding-left: 25%;
        outline: none;
        /*background: rgba(255,255,255,0);*/
        background-color: transparent;
        -webkit-appearance: none;
        -webkit-border-radius: 0px;
        font-size: 15px;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopTitle {
      padding-top: 70px;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopTitle .perfectInfoTopTitleSpan {
        color: #ffffff;
}
.perfectInfoContainer .perfectInfoTopBox .perfectInfoTopTitle .perfectInfoTopTitleSelect {
        float: right;
        color: #ffffff;
        width: 80%;
        border: none;
        border-bottom: 1px solid #ffffff;
        padding-bottom: 10px;
        padding-left: 25%;
        outline: none;
        /*background: rgba(255,255,255,0);*/
        background-color: transparent;
        -webkit-appearance: none;
        -webkit-border-radius: 0px;
        font-size: 15px;
}
.perfectInfoContainer .perfectInfoBottomButton {
    display: block;
    width: 90%;
    height: 45px;
    margin: auto;
    /*background:#529D98;*/
    background: transparent;
    color: #ffffff;
    border: 1px solid #86B8B8;
    border-radius: 22px;
    margin-top: 160px;
    outline: none;
    font-size: 16px;
}



