
.visitRrecord {
  height: 100vh;
  width: 100%;
  background: #fcfcfc;
  padding-top: 20px;
  padding-bottom: 20px;
}
.visitRrecord .visitRrecord-title-box {
    background: #5da096;
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 5px;
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.visitRrecord .visitRrecord-title-box .visitRrecord-title-text {
      color: #fff;
      font-size: 15px;
      display: inline-block;
      line-height: 30px;
}
.visitRrecord .visitRrecord-title-box .visitRrecord-setting-botton {
      color: #fff;
      font-size: 15px;
      margin-left: 80px;
      margin-right: 80px;
      display: inline-block;
      line-height: 30px;
      border: 1px solid #b5b5b5;
      background: #5da096;
}
.visitRrecord .visitRrecord-cue-box {
    width: 100%;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 10px;
}
.visitRrecord .visitRrecord-cue-box .visitRrecord-cue-text {
      color: #666666;
      font-size: 14px;
}
.visitRrecord .visitRrecord-list-box {
    margin: 10px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 50px #dbe5e4;
}
.visitRrecord .visitRrecord-list-box .visitRrecord-listheader-box {
      padding: 20px;
}
.visitRrecord .visitRrecord-list-box .visitRrecord-listheader-box .visitRrecord-listtitle-text {
        color: #232323;
        font-size: 16px;
}
.visitRrecord .visitRrecord-list-box .circleleft {
      top: 50px;
      left: 0;
      display: block;
}
.visitRrecord .visitRrecord-list-box .circleright {
      top: 50px;
      right: 0;
      display: block;
}
.visitRrecord .visitRrecord-list-box .visitRrecord-listcontent-box {
      padding: 20px;
      overflow: hidden;
}
.visitRrecord .visitRrecord-list-box .visitRrecord-listcontent-box .visitRrecord-listitem-left-text {
        color: #232323;
}
.visitRrecord .visitRrecord-list-box .visitRrecord-listcontent-box .visitRrecord-listitem-right-text {
        color: #232323;
}
.visitRrecord .visitRrecord-listitem-empty-box {
    width: 100%;
    text-align: center;
    line-height: 40px;
}
.visitRrecord .visitRrecord-listitem-empty-box .visitRrecord-listitem-empty-img {
      padding-top: 20vh;
      width: 70px;
      height: 70px;
}
.visitRrecord .visitRrecord-listitem-empty-box .visitRrecord-listitem-empty-text {
      display: inline-block;
      font-size: 16px;
      color: #d7d7d7;
}



