
.docChoose-ptient {
  min-height: 100vh;
  background: #FCFCFC;
}
.docChoose-ptient select {
    direction: rtl;
}
.docChoose-ptient select option {
    direction: ltr;
}
.docChoose-ptient .choose-content .closeHeight, .docChoose-ptient .sousuo-content .closeHeight {
    height: 0;
    width: 100%;
}
.docChoose-ptient .choose-content .showHeight, .docChoose-ptient .sousuo-content .showHeight {
    height: 100px;
}
.docChoose-ptient .choose-content .patient-list, .docChoose-ptient .sousuo-content .patient-list {
    background: #FCFCFC;
    width: 100%;
    overflow: hidden;
}
.docChoose-ptient .choose-content .patient-list .patient-box:nth-of-type(1), .docChoose-ptient .sousuo-content .patient-list .patient-box:nth-of-type(1) {
      box-shadow: inset -4px 6px 6px #D2E6E3;
}
.docChoose-ptient .choose-content .patient-list .patient-box, .docChoose-ptient .sousuo-content .patient-list .patient-box {
      font-size: 16px;
      color: #232323;
      width: 100%;
      border-bottom: 1px solid #bbb;
      padding: 16px 30px;
}
.docChoose-ptient .choose-content .patient-list .patient-box .icon-2, .docChoose-ptient .sousuo-content .patient-list .patient-box .icon-2 {
        vertical-align: middle;
        font-size: 18px;
        color: #529D98;
        padding-right: 10px;
}
.docChoose-ptient .choose-content .patient-list .patient-box .icon-yuan1, .docChoose-ptient .sousuo-content .patient-list .patient-box .icon-yuan1 {
        color: #bbb;
        padding-right: 10px;
}
.docChoose-ptient .choose-content .patient-list .patient-box img, .docChoose-ptient .sousuo-content .patient-list .patient-box img {
        height: 40px;
        width: 40px;
        border-radius: 50%;
        vertical-align: middle;
}
.docChoose-ptient .choose-content .patient-list .patient-box .patientName, .docChoose-ptient .choose-content .patient-list .patient-box .patientSex, .docChoose-ptient .sousuo-content .patient-list .patient-box .patientName, .docChoose-ptient .sousuo-content .patient-list .patient-box .patientSex {
        color: #232323;
        font-size: 15px;
        line-height: 33px;
}
.docChoose-ptient .choose-content .patient-list .patient-box .patientJibing, .docChoose-ptient .sousuo-content .patient-list .patient-box .patientJibing {
        border: 1px solid #fff;
        background: #bbb;
        color: #fff;
        font-size: 14px;
        padding: 1px 5px;
        border-radius: 10px;
}
.docChoose-ptient .choose-content .guanzhu-list, .docChoose-ptient .sousuo-content .guanzhu-list {
    margin-bottom: 160px;
}
.docChoose-ptient .choose-content .closeQian, .docChoose-ptient .choose-content .closeGuan, .docChoose-ptient .sousuo-content .closeQian, .docChoose-ptient .sousuo-content .closeGuan {
    display: none;
}
.docChoose-ptient .choose-content .input-box, .docChoose-ptient .sousuo-content .input-box {
    padding: 16px;
    background: #fff;
    border-bottom: 1px solid #ccc;
    font-size: 16px;
    color: #232323;
}
.docChoose-ptient .choose-content .input-box .paddingLeft, .docChoose-ptient .sousuo-content .input-box .paddingLeft {
      padding-left: 10px;
}
.docChoose-ptient .choose-content .input-box .icon-2, .docChoose-ptient .sousuo-content .input-box .icon-2 {
      color: #529D98;
      font-size: 18px;
}
.docChoose-ptient .choose-content .input-box .icon-yuan1, .docChoose-ptient .sousuo-content .input-box .icon-yuan1 {
      color: #bbb;
}
.docChoose-ptient .choose-content .input-box input, .docChoose-ptient .sousuo-content .input-box input {
      outline: none;
      display: none;
}
.docChoose-ptient .myPatientBox {
    height: 60px;
}
.docChoose-ptient .myPatientBox .mint-searchbar {
      background: #FCFCFC !important;
}
.docChoose-ptient .myPatientBox .mint-searchbar .mint-searchbar-inner {
        background: #ECECEC !important;
}
.docChoose-ptient .myPatientBox .mint-searchbar .mint-searchbar-inner .mintui-search {
          text-align: center;
}
.docChoose-ptient .myPatientBox .mint-searchbar .mint-searchbar-inner .mint-searchbar-cancel {
          color: #bbb !important;
}
.docChoose-ptient .myPatientBox .mint-searchbar .mint-searchbar-inner .mint-searchbar-core {
          background: #ECECEC;
}
.docChoose-ptient .myPatientBox .mint-searchbar .mint-searchbar-inner input::-webkit-input-placeholder {
          color: #D2D2D2;
          text-align: center;
}
.docChoose-ptient .myPatientBox .mint-searchbar .mint-searchbar-inner input {
          color: #232323;
}
.docChoose-ptient .footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin: auto;
}
.docChoose-ptient .footer .button-box {
      display: block;
      width: 95%;
      height: 45px;
      margin: 15px auto;
      background: #86B8B8;
      color: #ffffff;
      border: 1px solid #86B8B8;
      border-radius: 22px;
      outline: none;
      font-size: 18px;
}



