
.selectTestReportCheck {
  height: 100vh;
  width: 100%;
  background: #f4f4f4;
}
.selectTestReportCheck .selectTestReportCheck-titlebg-box {
    background: url(../../assets/img/selectTestReportCheck_bg.png) no-repeat center/cover;
    height: 30vh;
}
.selectTestReportCheck .selectTestReportCheck-list-box {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: -48px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 50px #dbe5e4;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
    height: 75%;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listheader-box {
      padding: 15px;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listheader-box .selectTestReportCheck-listtitle-text {
        color: #232323;
        font-size: 16px;
}
.selectTestReportCheck .selectTestReportCheck-list-box .circleleft {
      top: 40px;
      left: 0;
      display: block;
}
.selectTestReportCheck .selectTestReportCheck-list-box .circleright {
      top: 40px;
      right: 0;
      display: block;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listcontent-box {
      padding: 15px 10px;
      overflow: hidden;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listcontent-box .selectTestReportCheck-listcontent-yuan {
        margin-top: 1px;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listcontent-box .selectTestReportCheck-listitem-left-text {
        color: #232323;
        font-size: 15px;
        margin-top: 2px;
        margin-left: 5px;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listcontent-box .selectTestReportCheck-listitem-right-box .selectTestReportCheck-listitem-right-text {
        color: #999999;
        font-size: 15px;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listcontent-box .selectTestReportCheck-listitem-right-box .selectTestReportCheck-listitem-right-icon {
        margin-left: 5px;
        color: #b2b1b1;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listitem-empty-box {
      width: 100%;
      text-align: center;
      line-height: 40px;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listitem-empty-box .selectTestReportCheck-listitem-empty-img {
        padding-top: 10vh;
        width: 70px;
        height: 70px;
}
.selectTestReportCheck .selectTestReportCheck-list-box .selectTestReportCheck-listitem-empty-box .selectTestReportCheck-listitem-empty-text {
        display: inline-block;
        font-size: 16px;
        color: #d7d7d7;
}



