
@charset "UTF-8";
.doc-reservation-box {
  background-color: #f4f4f4;
  min-height: 100vh;
}
.doc-reservation-box .doc-reservation-list-box {
    /*margin: 20px;*/
    /*padding-top: 20px;*/
    margin-top: 20px;
    background-color: #fff;
    box-shadow: 0px 0px 5px #7e8c8d;
    /* 边框阴影效果 */
    border-radius: 3px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-top {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
      padding-top: 23px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-top .doc-reservation-list-content-top-left {
        color: #232323;
        font-size: 14px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-top .doc-reservation-list-content-top-left .doc-reservation-list-content-top-span {
          padding-left: 15px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-top .doc-reservation-list-content-top-left .doc-reservation-list-content-top-span-style {
          color: #fff;
          /*border:1px solid #60A39B;*/
          border-radius: 10px;
          padding: 1px 6px;
          margin-left: 4px;
          font-size: 14px;
          vertical-align: middle;
          background-color: #cfcfcf;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-top .doc-reservation-list-content-top-left .doc-reservation-list-content-top-name {
          font-size: 15px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-top .doc-reservation-list-content-top-right {
        font-size: 15px;
        padding-right: 15px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-mid {
      padding-top: 18px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-mid .doc-reservation-list-content-mid-top {
        padding-left: 15px;
        font-size: 14px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-mid .doc-reservation-list-content-mid-line {
        height: 1px;
        background: #eeeeee;
        margin-top: 18px;
        margin-left: 10px;
        margin-right: 10px;
}
.doc-reservation-box .doc-reservation-list-box .doc-reservation-list-content-bottom {
      padding-top: 15px;
      padding-bottom: 12px;
      text-align: right;
      padding-right: 15px;
      font-size: 15px;
      color: #D2D2D2;
}



