
@charset "UTF-8";
.doc-myinfo-container-box {
  position: fixed;
  /* 固定 */
  height: 100vh;
  width: 100%;
  background-image: url("../../assets/img/bgPerfectInfo.png");
  background-size: 100% 100%;
}
.doc-myinfo-container-box .doc-myinfo-top-box {
    color: #fff;
    font-size: 16px;
    margin: 10px 15px 0;
}
.doc-myinfo-container-box .doc-myinfo-top-box .doc-myinfo-top-name-box {
      padding-top: 20px;
      padding-bottom: 20px;
      border-bottom: 1px solid #bad6d3;
}
.doc-myinfo-container-box .doc-myinfo-top-box .doc-myinfo-top-name-box .doc-myinfo-top-name-right {
        float: right;
        font-size: 14px;
}
.doc-myinfo-container-box .doc-myinfo-bottom {
    color: #d4d4d4;
    font-size: 14px;
    /*clear: both;*/
    /*display: block;*/
    text-align: center;
    /*margin: 0px auto;*/
    position: absolute;
    bottom: 20px;
    width: 100%;
}



