
.doc-beizhu-box .beizhu-box {
  width: 90%;
  margin: 30px auto;
}
.doc-beizhu-box .beizhu-box .zhengzhuang {
    width: 100%;
    height: 200px;
    border: 1px solid #dedede;
    border-radius: 5px;
    text-indent: 5px;
    padding: 5px;
    line-height: 20px;
    font-size: 14px;
    color: #666;
    outline: none;
    box-sizing: border-box;
    -webkit-appearance: none;
    box-shadow: 0 0 5px 5px #E5EBED;
}
.doc-beizhu-box .beizhu-box .beizhu-num {
    position: absolute;
    top: 200px;
    left: 75%;
    color: #bbb;
    font-size: 15px;
}
.doc-beizhu-box .beizhu-box .button-box {
    display: block;
    width: 100%;
    height: 45px;
    margin: 45px auto;
    background: #86B8B8;
    color: #ffffff;
    border: 1px solid #86B8B8;
    border-radius: 22px;
    outline: none;
    font-size: 18px;
}
.doc-beizhu-box .beizhu-box .button-box.isButton {
    background: #529D98;
    border: 1px solid #f4f4f4;
    box-shadow: 0 0 10px #f4f4f4;
}



