
.integralAccount {
  height: 100vh;
  width: 100%;
  background: #fcfcfc;
}
.integralAccount .integralAccount-titlebg-box {
    background: #5fa39b;
    height: 26vh;
    text-align: center;
    padding-top: 30px;
}
.integralAccount .integralAccount-titlebg-box .visitRrecord-titletop-box .visitRrecord-title-icon {
      color: white;
      margin-left: 5px;
}
.integralAccount .integralAccount-titlebg-box .visitRrecord-title-text {
      color: #e5eeed;
      font-size: 17px;
      display: inline-block;
      line-height: 30px;
}
.integralAccount .integralAccount-titlebg-box .visitRrecord-money-text {
      color: white;
      font-size: 30px;
      display: inline-block;
      line-height: 40px;
}
.integralAccount .integralAccount-list-box {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: -55px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 50px #dbe5e4;
    padding-left: 10px;
    padding-right: 10px;
}
.integralAccount .integralAccount-list-box .integralAccount-listheader-box {
      padding: 18px;
}
.integralAccount .integralAccount-list-box .integralAccount-listheader-box .integralAccount-listtitle-text {
        color: #232323;
        font-size: 16px;
}
.integralAccount .integralAccount-list-box .circleleft {
      top: 48px;
      left: 0;
      display: block;
}
.integralAccount .integralAccount-list-box .circleright {
      top: 48px;
      right: 0;
      display: block;
}
.integralAccount .integralAccount-list-box .integralAccount-listcontent-box .integralAccount-listitem-box {
      overflow: hidden;
      padding: 15px;
}
.integralAccount .integralAccount-list-box .integralAccount-listcontent-box .integralAccount-listitem-box .integralAccount-listitem-left-text {
        color: #232323;
        font-size: 16px;
}
.integralAccount .integralAccount-list-box .integralAccount-listcontent-box .integralAccount-listitem-box .integralAccount-listitem-leftbottom-text {
        color: #999999;
        font-size: 15px;
        margin-top: 2px;
}
.integralAccount .integralAccount-list-box .integralAccount-listcontent-box .integralAccount-listitem-box .integralAccount-listitem-right-text {
        color: #232323;
        font-size: 16px;
}
.integralAccount .integralAccount-list-box .integralAccount-listcontent-box .integralAccount-listitem-box .integralAccount-listitem-right-red-text {
        color: #d80200;
        font-size: 16px;
}
.integralAccount .integralAccount-listitem-empty-box {
    width: 100%;
    text-align: center;
    line-height: 40px;
}
.integralAccount .integralAccount-listitem-empty-box .integralAccount-listitem-empty-img {
      padding-top: 10vh;
      width: 70px;
      height: 70px;
}
.integralAccount .integralAccount-listitem-empty-box .integralAccount-listitem-empty-text {
      display: inline-block;
      font-size: 16px;
      color: #d7d7d7;
}



