
@charset "UTF-8";
.doc-addBankCard-container-box {
  position: fixed;
  /* 固定 */
  height: 100vh;
  width: 100%;
  background-image: url("../../assets/img/bgPerfectInfo.png");
  background-size: 100% 100%;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box {
    margin: 30px 15px 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    border-bottom: 1px solid #bad6d3;
    padding-bottom: 20px;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-left-box {
      color: #ffffff;
      font-size: 16px;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-right-name-box {
      font-size: 14px;
      color: #ffffff;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-right-name-box .doc-addBankCard-header-right-input {
        background-color: transparent;
        color: #fff;
        FILTER: alpha(opacity=0);
        /*androd*/
        appearance: none;
        /*下拉框去掉右侧图标*/
        -moz-appearance: none;
        -webkit-appearance: none;
        text-align: right;
        outline: none;
        width: 100%;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-right-input-box {
      width: 70%;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-right-box {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-right-box .doc-addBankCard-header-right-smallbox {
        padding-right: 5px;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-right-box .doc-addBankCard-header-right-smallbox .doc-addBankCard-header-right-select {
          direction: rtl;
          font-size: 16px;
          -webkit-appearance: none;
          border: 0;
          outline: none;
          background-color: transparent;
          color: #ffffff;
}
.doc-addBankCard-container-box .doc-addBankCard-header-box .doc-addBankCard-header-right-box .doc-addBankCard-header-right-jiantou {
        font-size: 20px;
        color: #ffffff;
}
.doc-addBankCard-container-box .doc-addBankCard-bottom-button {
    display: block;
    width: 90%;
    height: 45px;
    margin: auto;
    border: 1px solid #86B8B8;
    border-radius: 22px;
    margin-top: 15px;
    outline: none;
    font-size: 16px;
    color: #fff;
    background-color: transparent;
}
.doc-addBankCard-container-box .doc-addBankCard-tip {
    color: #f8f8f8;
    padding-top: 60px;
    font-size: 14px;
    text-align: center;
}



