
html{
  height:100%;
  width:100%;
}
#loginBox{
  height:100vh;
  width:100%;
  background-image:url(../../assets/img/bacLogin.png);
  background-size:100% 100%;
  position: fixed;
}
.lineBox{
  height:1px;
  width:100%;
  background:#529D98;
}
.lineBox .line{
  height:1px;
  width:90%;
  margin:0 auto;
  background:#86B8B8;
}
#loginBox .fontBox{
  line-height:50px;
  color:#9BD3D0;
  background:#529D98;
}
#loginBox input{
  outline: none;
  border:none;
  font-size:16px;
  color:#9BD3D0;
  -webkit-appearance:none;
  padding-left:10px;
  background:#529D98;
}
input::-webkit-input-placeholder {
  color:#D7E7E2;
  font-size:16px;
}
.codeBox{
  width:100%;
  padding:0 5%;
  background:#529D98;
}
.codeInput{
  height:50px;
  line-height:50px;
}
.docPhone{
  position: relative;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  width:100%;
  padding:40px 5% 0;
  text-align: center;
  background:#529D98;
}
.docCode{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.docCodeFont{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  color: #D7E7E2;
  background: #529D98;
  max-width:65%;
  min-width:65%;
}
.getCode{
  padding-left:5px;
  line-height:50px;
  font-size:15px;
  color:#D7E7E2;
  border:none;
  background:#529D98;
  outline: none;
}
#loginBox .icon-shouji, .icon-anquan{
  font-size:22px;
  color:#fff;
}
.codeClose {
  height:18px;
  font-size:16px;
  display:inline-block;
  margin-left: -30%;
  color:#CAE1DF;
}
.getCode.active {
  background:#529D98;
}
.toInfo{
  display: block;
  width: 90%;
  height:45px;
  margin:25px auto;
  background:#529D98;
  color: #ffffff;
  border:1px solid #86B8B8;
  border-radius:22px;
  outline:none;
  font-size:18px;
}
.isButton {
  display: block;
  width: 90%;
  height:45px;
  margin:25px auto;
  background:#529D98;
  color: #ffffff;
  border:1px solid #86B8B8;
  border-radius:22px;
  outline:none;
  font-size:18px;
  background:rgba(82,157,152, 0.1);
  border:1px solid #86B8B8;
  /*background:#7e8c8d;*/
  box-shadow:0 0 10px #fff;
}




