
.docReport-detail .reportOne .reportDetailTop {
  padding: 10px;
  font-size: 15px;
  color: #232323;
  background: #fff;
}
.docReport-detail .reportOne .reportDetailTop .jianchaTime {
    padding-top: 8px;
}
.docReport-detail .reportOne section {
  padding-top: 10px;
  border-top: 10px solid #f4f4f4;
  background: #fff;
}
.docReport-detail .reportOne section .reply {
    width: 100%;
    position: fixed;
    bottom: 0;
    height: 40px;
    line-height: 30px;
    background: #f4f4f4;
    border-top: 1px solid #E2E2E4;
    padding: 10px 0;
}
.docReport-detail .reportOne section .reply .input-box {
      padding: 0 5px 10px;
      border-bottom: 1px solid #E2E2E4;
}
.docReport-detail .reportOne section .reply .input-box .fasong {
        padding: 6px 13px;
        background: #529D98;
        color: #fff;
        font-size: 15px;
        border-radius: 5px;
}
.docReport-detail .reportOne section .reply .input-box input {
        height: 35px;
        width: 80%;
        border: 1px solid #E2E2E4;
        border-radius: 5px;
        color: #232323;
        font-size: 15px;
        text-indent: 3px;
}
.docReport-detail .reportOne section .reply .input-box input::-webkit-input-placeholder {
        color: #bbb;
        font-size: 15px;
        text-indent: 3px;
}
.docReport-detail .reportOne section .reportTable {
    width: 100%;
    margin-bottom: 75px;
}
.docReport-detail .reportOne section .reportTable thead {
      color: #009fe9;
}
.docReport-detail .reportOne section .reportTable thead tr {
        text-align: center;
        height: 34px;
        line-height: 34px;
        font-size: 15px;
}
.docReport-detail .reportOne section .reportTable thead tr th:nth-of-type(1) {
          text-align: left;
          padding: 0 10px;
          word-break: keep-all;
}
.docReport-detail .reportOne section .reportTable thead tr td {
          font-size: 12px;
          color: #232323;
          padding: 1px;
}
.docReport-detail .reportOne section .reportTable tbody tr {
      text-align: center;
      height: 34px;
      line-height: 34px;
      font-size: 15px;
}
.docReport-detail .reportOne section .reportTable tbody tr td {
        font-size: 12px;
        color: #232323;
        padding: 1px;
}
.docReport-detail .reportOne section .reportTable tbody tr .resultUp {
        padding-left: 3px;
        color: #f00;
}
.docReport-detail .reportOne section .reportTable tbody tr td:nth-of-type(1) {
        text-align: left;
        padding-left: 5px;
        line-height: 15px;
        width: 33%;
        word-break: break-all;
}
.docReport-detail .reportOne section .reportTable tbody tr td:nth-of-type(2) {
        max-width: 20%;
        word-break: keep-all;
}
.docReport-detail .reportOne section .reportTable tbody tr td:nth-of-type(3) {
        max-width: 20%;
        word-break: keep-all;
}
.docReport-detail .reportOne section .reportTable tbody tr td:nth-of-type(4) {
        max-width: 20%;
        text-align: center;
        word-break: keep-all;
}



