
.testReportIdentityCard {
  height: 100vh;
  width: 100%;
  background-image: url(../../assets/img/bgPerfectInfo.png);
  background-size: 100% 100%;
  position: fixed;
}
.testReportIdentityCard .testReportIdentityCard-box {
    padding-left: 20px;
    padding-right: 20px;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-head-box {
      color: #ffffff;
      margin-top: 70px;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-input-box {
      width: 100%;
      padding-top: 40px;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-input-box .testReportIdentityCard-input {
        outline: none;
        font-size: 15px;
        color: white;
        background: transparent;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-input-box .testReportIdentityCard-line {
        height: 1px;
        width: 100%;
        background: #ffffff;
        margin-top: 10px;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-bottom-box {
      line-height: 20px;
      width: 100%;
      position: fixed;
      bottom: 50px;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-bottom-box .testReportIdentityCard-bottom-txt {
        color: #fff;
        width: 90%;
        font-size: 15px;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-bottom-box .toInfo {
        display: inline-block;
        width: 88%;
        height: 45px;
        line-height: 45px;
        text-align: center;
        background: rgba(82, 157, 152, 0.1);
        border: 1px solid #86B8B8;
        color: #ffffff;
        border-radius: 22px;
        margin-top: 15px;
        outline: none;
        font-size: 18px;
        box-shadow: 0 0 10px #fff;
}
.testReportIdentityCard .testReportIdentityCard-box .testReportIdentityCard-bottom-box .un-toInfo {
        display: inline-block;
        width: 88%;
        height: 45px;
        line-height: 45px;
        text-align: center;
        background: rgba(82, 157, 152, 0.1);
        border: 1px solid #86B8B8;
        color: #ffffff;
        border-radius: 22px;
        margin-top: 15px;
        outline: none;
        font-size: 18px;
}



