
.selectTestReport {
  height: 100vh;
  width: 100%;
  background: #f4f4f4;
}
.selectTestReport .selectTestReport-titlebg-box {
    background: url(../../assets/img/selectTestReportCheck_bg.png) no-repeat center/cover;
    height: 30vh;
}
.selectTestReport .selectTestReport-list-box {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: -48px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 50px #dbe5e4;
    padding-left: 10px;
    padding-right: 10px;
}
.selectTestReport .selectTestReport-list-box .selectTestReport-listheader-box {
      padding: 15px;
}
.selectTestReport .selectTestReport-list-box .selectTestReport-listheader-box .selectTestReport-listtitle-text {
        color: #232323;
        font-size: 16px;
}
.selectTestReport .selectTestReport-list-box .circleleft {
      top: 40px;
      left: 0;
      display: block;
}
.selectTestReport .selectTestReport-list-box .circleright {
      top: 40px;
      right: 0;
      display: block;
}
.selectTestReport .selectTestReport-list-box .selectTestReport-listcontent-box {
      padding: 15px 10px;
      overflow: hidden;
}
.selectTestReport .selectTestReport-list-box .selectTestReport-listcontent-box .selectTestReport-listcontent-yuan {
        margin-top: 1px;
}
.selectTestReport .selectTestReport-list-box .selectTestReport-listcontent-box .selectTestReport-listitem-left-text {
        color: #232323;
        font-size: 15px;
        margin-top: 2px;
        margin-left: 5px;
}
.selectTestReport .selectTestReport-list-box .selectTestReport-listcontent-box .selectTestReport-listitem-right-box .selectTestReport-listitem-right-text {
        color: #999999;
        font-size: 15px;
}
.selectTestReport .selectTestReport-list-box .selectTestReport-listcontent-box .selectTestReport-listitem-right-box .selectTestReport-listitem-right-icon {
        margin-left: 5px;
        color: #b2b1b1;
}



