
@charset "UTF-8";
.doc-appoint-container-box {
  padding-top: 25px;
}
.doc-appoint-container-box .appointment-time {
    margin: 22px 12px;
}
.doc-appoint-container-box .appointment-time .reserve-grids {
      background: #fff;
      position: relative;
      overflow: hidden;
}
.doc-appoint-container-box .appointment-time .reserve-grids .weui-grid {
        padding: 13px 0;
        position: relative;
        float: left;
        width: 33.33333333%;
        text-decoration: none;
        box-sizing: border-box;
}
.doc-appoint-container-box .appointment-time .reserve-grids .weui-grid .weui-grid__label {
          text-align: center;
          display: block;
          color: #666666;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          font-size: 15px;
}
.doc-appoint-container-box .appointment-time .reserve-grids .weui-grid .weui-grid__select-label {
          color: #5ac4b2;
}
.doc-appoint-container-box .appointment-time .reserve-grids .weui-grid:before {
        content: " ";
        position: absolute;
        right: 0;
        bottom: 0;
        color: #d9d9d9;
        top: 0;
        width: 1px;
        border-right: 1px solid #d9d9d9;
        -webkit-transform-origin: 100% 0;
        transform-origin: 100% 0;
        -webkit-transform: scaleX(0.5);
        transform: scaleX(0.5);
}
.doc-appoint-container-box .appointment-time .reserve-grids .weui-grid:after {
        left: 0;
        height: 1px;
        border-bottom: 1px solid #d9d9d9;
        -webkit-transform-origin: 0 100%;
        transform-origin: 0 100%;
        -webkit-transform: scaleY(0.5);
        transform: scaleY(0.5);
        content: " ";
        position: absolute;
        right: 0;
        bottom: 0;
        color: #d9d9d9;
}
.doc-appoint-container-box .appointment-time .reserve-grids .reserve-selected-grid {
        padding: 13px 0;
        background-color: #daf4fa;
}
.doc-appoint-container-box .appointment-time .weui-grids:before {
      right: 0;
      height: 1px;
      border-top: 1px solid #d9d9d9;
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
}
.doc-appoint-container-box .appointment-time .weui-grids:after {
      width: 1px;
      bottom: 0;
      border-left: 1px solid #d9d9d9;
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      -webkit-transform: scaleX(0.5);
      transform: scaleX(0.5);
}
.doc-appoint-container-box .appointment-time .weui-grids:after, .doc-appoint-container-box .appointment-time .weui-grids:before {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      color: #d9d9d9;
}
.doc-appoint-container-box .appointment-time .appointment-text {
      margin-bottom: 20px;
}
.doc-appoint-container-box .appointment-time .appointment-text .text-time {
        color: #232323;
        font-size: 16px;
        padding-left: 3px;
}
.doc-appoint-container-box .appointment-time .appointment-text .icon-jilu {
        font-size: 18px;
        color: #232323;
}
