
@charset "UTF-8";
.docErweima {
  height: 100vh;
  background: #529D98;
  padding: 20px 10px;
}
.docErweima .erweima-info {
    background: #fff;
    position: relative;
    border-radius: 5px;
}
.docErweima .erweima-info .docEeweima-img {
      padding: 10px 10px 20px;
      text-align: center;
}
.docErweima .erweima-info .docEeweima-img img {
        height: 230px;
        width: 230px;
}
.docErweima .erweima-info .docEeweima-img p {
        color: #bbb;
        font-size: 16px;
        padding-top: 20px;
}
.docErweima .erweima-info .docInfo {
      padding: 25px 16px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.docErweima .erweima-info .docInfo .docImg {
        padding-right: 5px;
}
.docErweima .erweima-info .docInfo .docImg img {
          height: 65px;
          width: 65px;
}
.docErweima .erweima-info .docInfo .docInfo-detail {
        padding-top: 15px;
}
.docErweima .erweima-info b:nth-of-type(1) {
      left: -1px;
      border-radius: 0 14px 14px 0;
}
.docErweima .erweima-info b:nth-of-type(2) {
      right: -1px;
      border-radius: 14px 0 0 14px;
}
.docErweima .erweima-info b {
      position: absolute;
      width: 5px;
      height: 10px;
      top: 40px;
      border: 1px solid #529D98;
      z-index: 33;
      background: #529D98;
      FILTER: alpha(opacity=0);
      /*androd*/
      appearance: none;
      /*下拉框去掉右侧图标*/
      -moz-appearance: none;
      -webkit-appearance: none;
}



