
.doc-cancelreason-top-box {
  margin: 30px 15px;
}
.doc-cancelreason-top-box .mint-radiolist-title {
    font-size: 18px;
    color: #000;
}
.doc-cancelreason-top-box .mint-cell-wrapper {
    background: #fff;
}
.doc-cancelreason-top-box .beizhu-box {
    width: 100%;
    margin: 25px auto;
}
.doc-cancelreason-top-box .beizhu-box .zhengzhuang {
      width: 100%;
      height: 150px;
      border: 1px solid #dedede;
      border-radius: 5px;
      text-indent: 0px;
      padding: 5px;
      line-height: 20px;
      font-size: 14px;
      color: #666;
      outline: none;
      box-sizing: border-box;
      -webkit-appearance: none;
      box-shadow: 0 0 5px 5px #E5EBED;
}
.doc-rev-detail-bottom-button {
  display: block;
  width: 90%;
  height: 45px;
  margin: auto;
  background: #62a39c;
  border: 1px solid #86B8B8;
  border-radius: 22px;
  margin-top: 44px;
  outline: none;
  font-size: 16px;
  color: #fff;
}



