
@charset "UTF-8";
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box {
  margin: 25px 15px 0;
  border-radius: 5px;
  background: #ffffff;
  position: relative;
  box-shadow: 0 0 19px #d4dadc;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-one-box {
    padding-top: 22px;
    padding-bottom: 16.5px;
    margin: 0px 15px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    border-bottom: 1px dashed #62a39c;
    line-height: 32px;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-one-box .doc-ConsultSetting-Header-one-font {
      font-size: 16px;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-two-box {
    padding-top: 22px;
    padding-bottom: 16.5px;
    margin: 0px 15px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    font-size: 16px;
    line-height: 32px;
    /* 选择服务费样式 */
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-two-box .doc-ConsultSetting-Header-two-font {
      color: #a0a0a0;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-two-box .doc-ConsultSetting-two-select-box {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-two-box .doc-ConsultSetting-two-select-box .doc-ConsultSetting-two-select-smallbox {
        padding-right: 5px;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-two-box .doc-ConsultSetting-two-select-box .doc-ConsultSetting-two-select-smallbox .doc-ConsultSetting-two-select {
          direction: rtl;
          font-size: 16px;
          -webkit-appearance: none;
          border: 0;
          outline: none;
          background-color: transparent;
          /*color: #ffffff;*/
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box .doc-ConsultSetting-Header-two-box .doc-ConsultSetting-two-select-box .doc-ConsultSetting-two-jiantou {
        font-size: 20px;
        color: #cccccc;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box b:nth-of-type(1) {
    left: -1px;
    border-radius: 0 14px 14px 0;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box b:nth-of-type(2) {
    right: -1px;
    border-radius: 14px 0 0 14px;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-Header-box b {
    position: absolute;
    width: 7px;
    height: 14px;
    top: 63px;
    border: 1px solid #f4f4f4;
    z-index: 33;
    background: #f4f4f4;
    FILTER: alpha(opacity=0);
    /*androd*/
    appearance: none;
    /*下拉框去掉右侧图标*/
    -moz-appearance: none;
    -webkit-appearance: none;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-button {
  display: block;
  width: 90%;
  height: 45px;
  margin: auto;
  background: #62a39c;
  border: 1px solid #86B8B8;
  border-radius: 22px;
  margin-top: 50px;
  outline: none;
  font-size: 16px;
  color: #fff;
}
.doc-ConsultSetting-Container-box .doc-ConsultSetting-tip {
  color: #6b6b6c;
  font-size: 14px;
  clear: both;
  display: block;
  /*text-align: center;*/
  margin: 27.5px 20px 0;
  position: absolute;
  /*bottom: 30px;*/
  /*width:100%;*/
  line-height: 25px;
}
.doc-ConsultSetting-Container-box .isClose {
  display: none;
}



