
@charset "UTF-8";
.doc-rev-detail-container-box {
  background-color: #f4f4f4;
  overflow: hidden;
  /* 解决高度塌陷问题(背景色不能充满屏幕,有空白)*/
  height: 100vh;
  width: 100%;
}
.doc-rev-detail-container-box .doc-rev-detail-top-box {
    font-size: 14px;
    margin: 20px 15px 0;
    background-color: #fff;
    box-shadow: 0px 0px 10px #7e8c8d;
    /* 边框阴影效果 */
    border-radius: 3px;
}
.doc-rev-detail-container-box .doc-rev-detail-top-box .doc-rev-detail-top-status-box {
      padding: 22px 0px;
      margin: 5px 10px;
      padding-bottom: 20px;
      border-bottom: 1px dashed #06b80a;
}
.doc-rev-detail-container-box .doc-rev-detail-top-box .doc-rev-detail-top-status-box .doc-rev-detail-top-status-span {
        float: right;
}
.doc-rev-detail-container-box .doc-rev-detail-top-box .doc-rev-detail-top-patientinfo-box {
      padding: 22px 0px;
      margin: 5px 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
      padding-bottom: 20px;
      border-bottom: 1px dashed #06b80a;
}
.doc-rev-detail-container-box .doc-rev-detail-top-box .doc-rev-detail-top-revtime-box {
      padding: 22px 0px;
      margin: 5px 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
.doc-rev-detail-container-box .doc-rev-detail-mid-box {
    font-size: 14px;
    margin: 12px 15px 0;
    background-color: #fff;
    box-shadow: 0px 0px 10px #7e8c8d;
    /* 边框阴影效果 */
    border-radius: 3px;
}
.doc-rev-detail-container-box .doc-rev-detail-mid-box .doc-rev-detail-mid-title {
      padding: 18px 0px;
      margin: 0px 8px;
}
.doc-rev-detail-container-box .doc-rev-detail-mid-box .doc-rev-detail-mid-title .icon-laba {
        color: #e1bd2f;
}
.doc-rev-detail-container-box .doc-rev-detail-bottom-button {
    display: block;
    width: 90%;
    height: 45px;
    margin: auto;
    background: #62a39c;
    border: 1px solid #86B8B8;
    border-radius: 22px;
    margin-top: 85px;
    outline: none;
    font-size: 16px;
    color: #fff;
}
.doc-rev-detail-container-box .doc-rev-detail-bottom-nobutton {
    color: #60A39B;
    padding-top: 35px;
    padding-right: 28px;
    font-size: 17px;
}
.doc-rev-detail-container-box .no-show {
    display: none;
}



