
@charset "UTF-8";
.doc-ApplyForCash-Container-box .doc-ApplyForCash-Header-Box {
  font-size: 16px;
  margin: 22px 15px 0;
  border-radius: 5px;
  background: #ffffff;
  position: relative;
  box-shadow: 0 0 25px #d4dadc;
}
.doc-ApplyForCash-Container-box .doc-ApplyForCash-Header-Box .doc-ApplyForCash-Header-left {
    padding: 20px 15px 20px;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box {
  margin: 15px 15px 0;
  border-radius: 5px;
  background: #ffffff;
  position: relative;
  margin-bottom: 15px;
  box-shadow: 0 0 19px #d4dadc;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box .doc-applyforcash-mid-header-box {
    font-size: 16px;
    margin: 15px 15px 15px;
    border-bottom: 1px dashed #62a39c;
    padding: 21px 0px 21px;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box .doc-applyforcash-mid-mid-box {
    font-size: 15px;
    margin: 15px 15px 15px;
    padding: 15px 0px 20px;
    border-bottom: 1px dashed #62a39c;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box .doc-applyforcash-mid-mid-box .doc-applyforcash-mid-money {
      padding: 30px 0 0;
      color: #5da096;
      font-size: 27.5px;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box .doc-applyforcash-bottom-box {
    margin: 15px 15px 15px;
    padding: 5px 0px 25px;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box b:nth-of-type(1) {
    left: -1px;
    border-radius: 0 14px 14px 0;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box b:nth-of-type(2) {
    right: -1px;
    border-radius: 14px 0 0 14px;
}
.doc-ApplyForCash-Container-box .doc-applyforcash-mid-box b {
    position: absolute;
    width: 7px;
    height: 14px;
    top: 40px;
    border: 1px solid #f4f4f4;
    z-index: 33;
    background: #f4f4f4;
    FILTER: alpha(opacity=0);
    /*androd*/
    appearance: none;
    /*下拉框去掉右侧图标*/
    -moz-appearance: none;
    -webkit-appearance: none;
}



