
.docFaTongzhi {
  min-height: 100vh;
  background: #FCFCFC;
  width: 100%;
  padding-top: 15px;
}
.docFaTongzhi .xuanze-patient {
    width: 85%;
    margin: 16px auto;
    padding: 5px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 5px 5px #E5EBED;
    font-size: 16px;
    color: #232323;
    line-height: 38px;
}
.docFaTongzhi .xuanze-patient .patient-name, .docFaTongzhi .xuanze-patient .patient-num {
      font-size: 14px;
      color: #bbb;
}
.docFaTongzhi .xuanze-patient .icon-jiahao {
      font-size: 28px;
      color: #bbb;
}
.docFaTongzhi .beizhu-box {
    width: 90%;
    margin: 30px auto;
}
.docFaTongzhi .beizhu-box .zhengzhuang {
      width: 100%;
      height: 200px;
      border: 1px solid #dedede;
      border-radius: 5px;
      text-indent: 5px;
      padding: 5px;
      line-height: 20px;
      font-size: 14px;
      color: #666;
      outline: none;
      box-sizing: border-box;
      -webkit-appearance: none;
      box-shadow: 0 0 5px 5px #E5EBED;
}
.docFaTongzhi .beizhu-box .beizhu-num {
      position: absolute;
      top: 280px;
      left: 75%;
      color: #bbb;
      font-size: 15px;
}
.docFaTongzhi .beizhu-box .button-box {
      position: fixed;
      bottom: 25px;
      display: block;
      width: 90%;
      height: 45px;
      margin: auto;
      background: #86B8B8;
      color: #ffffff;
      border: 1px solid #86B8B8;
      border-radius: 22px;
      outline: none;
      font-size: 18px;
}
.docFaTongzhi .beizhu-box .button-box.isButton {
      background: #529D98;
      border: 1px solid #f4f4f4;
      box-shadow: 0 0 10px #f4f4f4;
}



