
@charset "UTF-8";
.doc-visi-container-box {
  overflow: hidden;
  background: #f4f4f4;
}
.doc-visi-container-box .doc-visi-header-box {
    margin: 20px 20px 0;
    font-size: 15px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    background: #5da096;
    height: 80px;
    border-radius: 7px;
}
.doc-visi-container-box .doc-visi-header-box .doc-visi-title {
      padding-top: 15px;
      text-align: center;
      color: #ffffff;
}
.doc-visi-container-box .doc-visi-header-box .doc-visi-youjiantou {
      right: 10px;
      line-height: 80px;
}
.doc-visi-container-box .doc-visi-header-box .doc-visit-noset-title {
      padding-top: 15px;
      text-align: center;
      color: rgba(255, 255, 255, 0.5);
}
.doc-visi-container-box .doc-visi-header-box .doc-visit-noset-button {
      border: 1px solid #ffffff;
      border-radius: 20px;
      font-size: 14px;
      color: #ffffff;
      width: 86px;
      margin: auto;
      -webkit-appearance: none;
      background-color: transparent;
      outline: none;
}
.doc-visi-container-box .doc-visi-list-box {
    margin: 15px 20px 0;
    border-radius: 5px;
    background: #ffffff;
    position: relative;
    box-shadow: 0 0 25px #d4dadc;
}
.doc-visi-container-box .doc-visi-list-box .doc-visi-list-title-box {
      padding: 15px 20px;
      border-bottom: 1px dashed #62a39c;
}
.doc-visi-container-box .doc-visi-list-box .doc-visi-list-cell {
      padding: 20px 20px 35px;
      color: #666666;
}
.doc-visi-container-box .doc-visi-list-box b {
      position: absolute;
      top: 40px;
      width: 7px;
      height: 14px;
      border: 1px solid #f4f4f4;
      z-index: 33;
      background: #f4f4f4;
      FILTER: alpha(opacity=0);
      /*androd*/
      appearance: none;
      /*下拉框去掉右侧图标*/
      -moz-appearance: none;
      -webkit-appearance: none;
}
.doc-visi-container-box .doc-visi-list-box b:nth-of-type(1) {
      left: 0px;
      border-radius: 0 14px 14px 0;
}
.doc-visi-container-box .doc-visi-list-box b:nth-of-type(2) {
      right: 0px;
      border-radius: 14px 0 0 14px;
}



